// app/history/page.tsx
"use client";

import React, { useEffect, useMemo, useRef, useState } from "react";
import Link from "next/link";
import {
  ClockIcon,
  DocumentMagnifyingGlassIcon,
  ArrowPathIcon,
  ExclamationTriangleIcon,
  EyeIcon,
  TrashIcon,
  FunnelIcon,
  MagnifyingGlassIcon,
  ChevronRightIcon,
  CheckIcon,
  XMarkIcon,
  ChartPieIcon,
  Bars3BottomLeftIcon,
  InformationCircleIcon,
} from "@heroicons/react/24/outline";

/* ================= TYPES ================= */

type ComparisonItem = {
  id: number;
  document_name: string;
  version_old_label: string;
  version_new_label: string;
  created_at: string;
  overall_risk_level?: string | null;
  changes_count?: number;
};

type DateFilter = "ALL" | "TODAY" | "WEEK" | "MONTH";

const API_BASE = "http://127.0.0.1:8000";

/* ================= PAGE ================= */

export default function HistoryPage() {
  const [items, setItems] = useState<ComparisonItem[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [selected, setSelected] = useState<number[]>([]);

  // filter
  const [search, setSearch] = useState("");
  const [riskFilter, setRiskFilter] = useState("ALL");
  const [dateFilter, setDateFilter] = useState<DateFilter>("ALL");

  // graph
  const [hovered, setHovered] = useState<ComparisonItem | null>(null);
  const [showGraph, setShowGraph] = useState(false);
  const canvasRef = useRef<HTMLCanvasElement>(null);

  /* ================= DATA ================= */

  useEffect(() => {
    load();
  }, []);

  const load = async () => {
    setLoading(true);
    setError(null);
    try {
      const res = await fetch(`${API_BASE}/comparisons?limit=100`);
      if (!res.ok) throw new Error("โหลดข้อมูลไม่สำเร็จ");
      setItems(await res.json());
    } catch (e: any) {
      setError(e.message || "เกิดข้อผิดพลาด");
    } finally {
      setLoading(false);
    }
  };

  const filtered = useMemo(() => {
    return items.filter((i) => {
      if (search && !i.document_name.toLowerCase().includes(search.toLowerCase()))
        return false;
      if (riskFilter !== "ALL" && i.overall_risk_level !== riskFilter)
        return false;

      if (dateFilter !== "ALL") {
        const d = new Date(i.created_at);
        const now = new Date();
        const diff = (now.getTime() - d.getTime()) / (1000 * 3600 * 24);
        if (dateFilter === "TODAY" && diff > 1) return false;
        if (dateFilter === "WEEK" && diff > 7) return false;
        if (dateFilter === "MONTH" && diff > 30) return false;
      }
      return true;
    });
  }, [items, search, riskFilter, dateFilter]);

  /* ================= GRAPH ================= */

  useEffect(() => {
    if (showGraph && hovered) drawGraph();
  }, [showGraph, hovered]);

  const drawGraph = () => {
    const c = canvasRef.current;
    if (!c) return;
    const ctx = c.getContext("2d");
    if (!ctx) return;

    const w = c.width;
    const h = c.height;
    const cx = w / 2;
    const cy = h / 2;
    const r = Math.min(w, h) * 0.32;

    ctx.clearRect(0, 0, w, h);

    const risk = hovered?.overall_risk_level || "LOW";
    const color =
      risk === "HIGH"
        ? "#ef4444"
        : risk === "MEDIUM"
        ? "#f59e0b"
        : "#10b981";

    const fill =
      risk === "HIGH"
        ? "rgba(239,68,68,0.25)"
        : risk === "MEDIUM"
        ? "rgba(245,158,11,0.25)"
        : "rgba(16,185,129,0.25)";

    const values = [
      risk === "HIGH" ? 0.9 : risk === "MEDIUM" ? 0.65 : 0.4, // Legal
      risk === "HIGH" ? 0.8 : risk === "MEDIUM" ? 0.6 : 0.35, // Financial
      risk === "HIGH" ? 0.75 : risk === "MEDIUM" ? 0.55 : 0.3, // Operational
    ];

    const labels = ["LEGAL", "FINANCIAL", "OPERATIONAL"];
    const angles = [-Math.PI / 2, (2 * Math.PI) / 3 - Math.PI / 2, (4 * Math.PI) / 3 - Math.PI / 2];

    // grid
    ctx.strokeStyle = "#e5e7eb";
    for (let i = 1; i <= 4; i++) {
      ctx.beginPath();
      ctx.moveTo(
        cx + r * (i / 4) * Math.cos(angles[0]),
        cy + r * (i / 4) * Math.sin(angles[0])
      );
      angles.forEach((a, idx) => {
        const na = angles[(idx + 1) % 3];
        ctx.lineTo(
          cx + r * (i / 4) * Math.cos(na),
          cy + r * (i / 4) * Math.sin(na)
        );
      });
      ctx.closePath();
      ctx.stroke();
    }

    // axis
    ctx.strokeStyle = "#94a3b8";
    angles.forEach((a) => {
      ctx.beginPath();
      ctx.moveTo(cx, cy);
      ctx.lineTo(cx + r * Math.cos(a), cy + r * Math.sin(a));
      ctx.stroke();
    });

    // polygon
    ctx.beginPath();
    values.forEach((v, i) => {
      const x = cx + r * v * Math.cos(angles[i]);
      const y = cy + r * v * Math.sin(angles[i]);
      i === 0 ? ctx.moveTo(x, y) : ctx.lineTo(x, y);
    });
    ctx.closePath();
    ctx.fillStyle = fill;
    ctx.strokeStyle = color;
    ctx.lineWidth = 3;
    ctx.shadowColor = color;
    ctx.shadowBlur = 15;
    ctx.fill();
    ctx.stroke();
    ctx.shadowBlur = 0;

    // points
    values.forEach((v, i) => {
      const x = cx + r * v * Math.cos(angles[i]);
      const y = cy + r * v * Math.sin(angles[i]);
      ctx.beginPath();
      ctx.arc(x, y, 6, 0, Math.PI * 2);
      ctx.fillStyle = "#fff";
      ctx.fill();
      ctx.strokeStyle = color;
      ctx.lineWidth = 2;
      ctx.stroke();
    });

    // labels
    ctx.fillStyle = "#334155";
    ctx.font = "bold 12px Inter";
    labels.forEach((lb, i) => {
      const x = cx + (r + 32) * Math.cos(angles[i]);
      const y = cy + (r + 32) * Math.sin(angles[i]);
      ctx.fillText(lb, x - 20, y);
    });
  };

  /* ================= UI ================= */

  const RiskBadge = ({ item }: { item: ComparisonItem }) => {
    const lv = item.overall_risk_level || "LOW";
    const base =
      "px-3 py-1.5 rounded-full text-xs font-bold cursor-pointer transition-all";

    const style =
      lv === "HIGH"
        ? "bg-red-100 text-red-700 hover:shadow-red-400/50"
        : lv === "MEDIUM"
        ? "bg-amber-100 text-amber-700 hover:shadow-amber-400/50"
        : "bg-emerald-100 text-emerald-700 hover:shadow-emerald-400/50";

    return (
      <span
        onMouseEnter={() => {
          setHovered(item);
          setShowGraph(true);
        }}
        onMouseLeave={() => {
          setTimeout(() => setShowGraph(false), 200);
        }}
        className={`${base} ${style} hover:scale-105 shadow-lg`}
      >
        {lv}
      </span>
    );
  };

  /* ================= RENDER ================= */

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 p-6">
      {showGraph && hovered && (
        <div className="fixed inset-0 z-50 flex items-center justify-center">
          <div className="absolute inset-0 bg-black/30 backdrop-blur-sm" />
          <div className="relative bg-white/90 backdrop-blur-xl rounded-3xl shadow-2xl border p-6 w-[420px] animate-scale-in">
            <div className="flex justify-between items-center mb-4">
              <div className="font-bold text-slate-900 flex items-center gap-2">
                <ChartPieIcon className="h-5 w-5 text-blue-600" />
                Risk Intelligence
              </div>
              <button onClick={() => setShowGraph(false)}>
                <XMarkIcon className="h-5 w-5 text-slate-500" />
              </button>
            </div>

            <div className="text-sm font-semibold text-slate-700 mb-2">
              {hovered.document_name}
            </div>

            <canvas ref={canvasRef} width={360} height={360} />

            <div className="mt-4 text-xs text-slate-600 flex items-center gap-2">
              <InformationCircleIcon className="h-4 w-4" />
              Legal • Financial • Operational Risk Overview
            </div>

            <Link
              href={`/compare/${hovered.id}`}
              className="mt-4 block text-center py-2 rounded-xl bg-blue-600 text-white font-bold hover:bg-blue-700"
            >
              ดูรายละเอียดเชิงลึก
            </Link>
          </div>
        </div>
      )}

      {/* header */}
      <div className="max-w-7xl mx-auto">
        <div className="flex items-center gap-4 mb-6">
          <div className="h-14 w-14 rounded-2xl bg-blue-600 flex items-center justify-center shadow-lg">
            <ClockIcon className="h-7 w-7 text-white" />
          </div>
          <div>
            <h1 className="text-3xl font-bold text-slate-900">
              Document Risk History
            </h1>
            <p className="text-slate-600">
              Executive-level Legal / Financial / Operational Intelligence
            </p>
          </div>
        </div>

        {/* table */}
        <div className="bg-white rounded-2xl shadow-sm border overflow-hidden">
          <table className="w-full">
            <thead className="bg-slate-50 text-sm font-bold text-slate-700">
              <tr>
                <th className="p-4 text-left">Document</th>
                <th className="p-4">Version</th>
                <th className="p-4">Date</th>
                <th className="p-4">Risk</th>
                <th className="p-4">Action</th>
              </tr>
            </thead>
            <tbody>
              {filtered.map((i) => (
                <tr key={i.id} className="border-t hover:bg-slate-50">
                  <td className="p-4 font-semibold">{i.document_name}</td>
                  <td className="p-4 text-sm">
                    {i.version_old_label} → {i.version_new_label}
                  </td>
                  <td className="p-4 text-sm">
                    {new Date(i.created_at).toLocaleString("th-TH")}
                  </td>
                  <td className="p-4">
                    <RiskBadge item={i} />
                  </td>
                  <td className="p-4">
                    <Link
                      href={`/compare/${i.id}`}
                      className="p-2 rounded-lg hover:bg-blue-50 inline-block"
                    >
                      <EyeIcon className="h-5 w-5 text-blue-600" />
                    </Link>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>

          {loading && (
            <div className="p-6 text-center text-slate-600">Loading…</div>
          )}
          {error && (
            <div className="p-6 text-center text-red-600">{error}</div>
          )}
        </div>
      </div>
    </div>
  );
}
